import React from 'react';
import {
    Layout,
    Breadcrumb,
    Card
} from 'antd';
import TopMenu from './TopMenu';
import HeaderPage from '../components/Header'
import capitalizeFirstCharacter from '../helpers/capitalizeFirstCharacter'
import dashToSpace from '../helpers/dashToSpace'

import { connect } from 'react-redux';
import { setAuthObject } from '../redux/actions/setAuthObjectAction';
import Api from '../helpers/api';
import store from "../redux/store";
import handleApiError from '../helpers/handleApiError';
import { withRouter } from 'react-router-dom/cjs/react-router-dom.min';

const { Content } = Layout;
const api = new Api();

class Template extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            breadcumbs: [],
            authObject: []
        }
        this.getBreadcumbs.bind(this)
    }

    componentDidMount() {
        this.getBreadcumbs()
        this.getAuthObject()
    }

    getBreadcumbs = () => {
        const { props } = this.props.children
        let getUrls = props.location.pathname.split('/')

        console.log('props', props)
        console.log('urls', getUrls)

        let breadcumbs = []
        getUrls.map((item) => {
            if (item) {
                let object = dashToSpace(item)
                let upperCaseFirstCharacterObject = capitalizeFirstCharacter(object)

                breadcumbs.push(upperCaseFirstCharacterObject)
            }
        })

        let newBreadcumbs = { ...this.state, breadcumbs }

        this.setState({
            breadcumbs: newBreadcumbs.breadcumbs
        })
    }

    getAuthObject = async () => {
        const { authObject } = store.getState()
        if (authObject.data.length < 1) {
            await api.getAuthObjectList()
                .then(response => {
                    const { data } = response
                    if (response.status === 200) { //OK
                        store.dispatch(setAuthObject(data))
                        this.setState({ authObject: data })
                    }
                })
                .catch(error => {
                    handleApiError(error, this.props)
                    console.log(error.response)
                })
        } else {
            this.setState({ authObject: authObject.data })
        }
    }


    render() {
        const { breadcumbs } = this.state
        const { children } = this.props
        return (
            <Layout>
                <HeaderPage />
                {this.state.authObject.length ?
                    <Content>
                        <TopMenu />
                        {this.props.location.pathname !== '/home' &&
                            <Card style={{ margin: '15px' }}>
                                <Breadcrumb>
                                    {breadcumbs.map((item, index) => {
                                        if (index <= 1) {
                                            return (
                                                <Breadcrumb.Item key={index}>
                                                    <span>{item}</span>
                                                </Breadcrumb.Item>
                                            )
                                        }
                                    })}
                                </Breadcrumb>
                            </Card>
                        }

                        <div style={{ margin: '15px' }}>
                            {children}
                        </div>
                    </Content>
                    : null}
            </Layout>
        )
    }
}

export default withRouter(connect(
    null,
    { setAuthObject }
)(Template));