import React, { useState, Fragment, useEffect } from 'react';
import { Menu } from 'antd';
import { Link, withRouter } from 'react-router-dom';
import hasAuthObject from '../../helpers/hasAuthObject';

const authobjectCfg = {
    roomView: 'room-menu',
    plantView: 'plant-menu',
    vendorView: 'vendor-menu',
    uomView: 'unit-of-measurement-menu',
    classificationView: 'classification-menu',
    materialView: 'material-menu',
    materialSourcingView: 'material-sourcing-menu',
    stockView: 'stock-menu',
    reservationView: 'reservation-menu',
    purchaseOrderView: 'purchase-order-menu',
    deliveryOrderView: 'delivery-order-menu',
    goodReceiveView: 'good-receives-menu',
    stockOpnameView: 'stock-opname-menu',
    userView: 'user-menu',
    roleView: 'role-menu',
}

const { SubMenu } = Menu;
const TopMenu = (props) => {

    const [selectedkey, setSelectedKey] = useState("")

    useEffect(() => {
        activeMenu()
    }, [selectedkey])

    const activeMenu = () => {
        let location = props.location.pathname.split('/')
        let index = location.length > 3 ? -2 : -1
        let key = `${location.slice(0, index).join('/')}/list`
        setSelectedKey(key)
    }

    return (
        <Fragment>
            <Menu mode="horizontal" selectedKeys={[selectedkey]}>

                <Menu.Item key="/list">
                    Home
                    <Link to='/home' />
                </Menu.Item>

                {hasAuthObject(authobjectCfg.stockOpnameView) &&
                    <Menu.Item key="/stock-opname/list">
                        Stock Opname
                        <Link to='/stock-opname/list' />
                    </Menu.Item>
                }

                {hasAuthObject(authobjectCfg.stockView) &&
                    <Menu.Item key="/stock/list">Stock
                        <Link to='/stock/list' />
                    </Menu.Item>
                }

                {hasAuthObject(authobjectCfg.reservationView) &&
                    <Menu.Item key="/reservation/list">
                        Reservation
                        <Link to='/reservation/list' />
                    </Menu.Item>
                }

                {hasAuthObject(authobjectCfg.purchaseOrderView) &&
                    <Menu.Item key="/purchase-order/list">
                        Purchase Order
                        <Link to='/purchase-order/list' />
                    </Menu.Item>
                }

                {hasAuthObject(authobjectCfg.deliveryOrderView) &&
                    <Menu.Item key="/delivery-order/list">
                        Delivery Order
                        <Link to='/delivery-order/list' />
                    </Menu.Item>
                }

                {hasAuthObject(authobjectCfg.goodReceiveView) &&
                    <Menu.Item key="/good-receives/list">
                        Good Receives
                        <Link to='/good-receives/list' />
                    </Menu.Item>
                }

                {hasAuthObject(authobjectCfg.classificationView)
                    || hasAuthObject(authobjectCfg.materialView)
                    || hasAuthObject(authobjectCfg.materialSourcingView)
                    ?
                    <SubMenu key="material" title="Material">
                        {hasAuthObject(authobjectCfg.classificationView) &&
                            <Menu.Item key="/classification-material/list">Classification
                                <Link to='/classification-material/list' />
                            </Menu.Item>
                        }
                        {hasAuthObject(authobjectCfg.materialView) &&
                            <Menu.Item key="/material/list">Material Master
                                <Link to='/material/list' />
                            </Menu.Item>
                        }
                        {hasAuthObject(authobjectCfg.materialSourcingView) &&
                            <Menu.Item key="/material-sourcing/list">Material Sourcing
                                <Link to='/material-sourcing/list' />
                            </Menu.Item>
                        }
                    </SubMenu>
                    : null
                }

                {hasAuthObject(authobjectCfg.roomView)
                    || hasAuthObject(authobjectCfg.plantView)
                    || hasAuthObject(authobjectCfg.uomView)
                    ?
                    <SubMenu key="master" title="Master">
                        {hasAuthObject(authobjectCfg.plantView) &&
                            <Menu.Item key="/plant/list">Plant
                                <Link to='/plant/list' />
                            </Menu.Item>
                        }
                        {hasAuthObject(authobjectCfg.roomView) &&
                            <Menu.Item key="/room/list">Room
                                <Link to='/room/list' />
                            </Menu.Item>
                        }
                        {hasAuthObject(authobjectCfg.vendorView) &&
                            <Menu.Item key="/vendor/list">Vendor
                                <Link to='/vendor/list' />
                            </Menu.Item>
                        }
                        {hasAuthObject(authobjectCfg.uomView) &&
                            <Menu.Item key="/uom/list">Unit of Measurement
                                <Link to='/uom/list' />
                            </Menu.Item>
                        }
                    </SubMenu>
                    : null
                }

                {hasAuthObject(authobjectCfg.userView)
                    || hasAuthObject(authobjectCfg.roleView)
                    ?
                    <SubMenu key="system" title="System">
                        {hasAuthObject(authobjectCfg.userView) &&
                            <Menu.Item key="/user/list">User
                                <Link to='/user/list' />
                            </Menu.Item>
                        }
                        {hasAuthObject(authobjectCfg.roleView) &&
                            <Menu.Item key="/role/list">Role
                                <Link to='/role/list' />
                            </Menu.Item>
                        }
                    </SubMenu>
                    : null
                }
            </Menu>
        </Fragment>
    )
}

export default withRouter(TopMenu)