import React, { useState } from "react"
import {
    Col, Layout, Row,
    Dropdown, Avatar, Menu
} from 'antd'
import './index.css'
import { withRouter } from "react-router";
import { UserOutlined, LogoutOutlined } from '@ant-design/icons';
import store from "../../redux/store";
import { useEffect } from "react";
import { connect } from 'react-redux';
import { setAuthObject } from "../../redux/actions/setAuthObjectAction";
import { Link } from "react-router-dom";

const { Header } = Layout


const HeaderPage = (props) => {

    const [collapsed, setCollapsed] = useState(false)
    const [user, setUser] = useState({})

    useEffect(() => {
        getUser()
    }, [])

    const getUser = () => {
        let user = JSON.parse(localStorage.getItem('user'));
        setUser(user)
    }

    const toggle = () => {
        setCollapsed(!collapsed)
    }

    const logout = () => {
        localStorage.clear()
        props.history.push('/login')
        store.dispatch(setAuthObject([]))
    }

    const onUserMenuClick = ({ key }) => {
        switch (key) {
            case 'profile':
                // this.props.history.push('/profile')
                break
            case 'logout':
                logout()
                break
            default:
                break
        }
    }

    const userMenu = (
        <Menu onClick={onUserMenuClick}>
            <Menu.Item key="profile">
                {/* <SettingOutlined /> Profile */}
                <UserOutlined />
                {user.email}
            </Menu.Item>
            <Menu.Divider />
            <Menu.Item key="logout">
                <LogoutOutlined />
                Logout
            </Menu.Item>
        </Menu>
    )

    return (
        <Header className="header"
        // style={{ background: config.headerColor }}
        >
            <Row type="flex">
                <Col md={{ span: 20 }}>
                    <Link to="/home">
                        <img src={require('../../assets/img/inventaris-net.png')} style={{ width: 'auto', height: 45, marginLeft: 15 }} />
                    </Link>
                </Col>
                <Col md={4} style={{ paddingRight: 10 }}>
                    <Col md={{ span: 6, offset: 4 }} >
                        <Dropdown
                            overlay={userMenu}
                        >
                            <div style={{ cursor: 'pointer' }}>
                                <Avatar
                                    src={user ? user.photo_url_full : null}
                                    style={{ backgroundColor: '#aaa', verticalAlign: 'middle', marginRight: 5 }} size="medium">
                                    {user && user.fullname ? user.fullname.charAt(0) : ''}
                                </Avatar>
                            </div>
                        </Dropdown>
                    </Col>
                </Col>
            </Row>
        </Header>
    )
}


export default withRouter(connect(
    null,
    { setAuthObject }
)(HeaderPage));